.heading {
  position: relative;
  padding-top: 63px;
  padding-bottom: 72px;
  background-image: url("../img/heading-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 10;
}

.heading::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(38, 45, 51, 0.75), rgba(38, 45, 51, 0.75));
  z-index: -1;
}

.heading__container-s {
  position: relative;
}

.heading__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.heading__tag {
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #4592FF;
}

.heading__title {
  max-width: 80%;
  margin-bottom: 32px;
  font-family: "Roboto Slab", sans-serif;
  font-size: 55px;
  line-height: 70px;
  color: #fff;
}

.heading__text {
  max-width: 67%;
  font-size: 18px;
  line-height: 30px;
  color: #fff;
  mix-blend-mode: normal;
  opacity: 0.85;
}

.heading__bottom {
  display: flex;
  align-items: center;
  margin-top: 41px;
}

.heading__icon {
  width: 22px;
  height: 27px;
  left: -93px;
  top: 88px;
}

.heading__like {
  margin-right: 20px;
}

.navigation {
  display: flex;
  align-items: center;
}

.navigation__link {
  color: #fff;
  font-weight: 900;
  font-size: 14px;
  display: flex;
  align-items: center;
}

.navigation__link:first-child {
  margin-right: 31px;
}

.navigation__link:nth-child(2) {
  margin-right: 22px;
}

.navigation__home {
  fill: #fff;
  width: 18px;
  height: 17px;
  margin-right: 6px;
}

.navigation__next {
  fill: #fff;
  width: 15px;
  height: 7px;
  margin-left: 10px;
}

.navigation__prev {
  fill: #fff;
  width: 15px;
  height: 7px;
  margin-right: 9px;
}

.clock {
  margin-right: 22px;
  display: flex;
  align-items: center;
}

.clock__img {
  margin-right: 5px;
  width: 15px;
  height: 15px;
  opacity: 0.4;
}

.clock__date {
  color: #ffffff;
  opacity: 0.85;
  font-size: 12px;
  mix-blend-mode: normal;
}

.like__counter--article {
  color: #ffffff;
  opacity: 0.85;
  font-size: 12px;
  mix-blend-mode: normal;
}

.comment__counter--article {
  color: #ffffff;
  opacity: 0.85;
  font-size: 12px;
  mix-blend-mode: normal;
}
